<template>
  <div>
    <Head title="商品详情" :show="true"></Head>
    <div v-if="obj">
      <img :src="obj.img" class="img" alt />
      <h4 class="h4">{{obj.name}} -- {{obj.price}} / type = {{obj.type.text}}</h4>
      数量 <van-stepper v-model="count" />
    </div>

    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" @click="callOne" />
      <van-goods-action-icon icon="cart-o" :info="total" text="购物车" @click="gotoCart" />
      <van-goods-action-button type="warning" text="加入购物车" @click="addToshopcar" />
      <van-goods-action-button type="danger" text="立即购买" @click="paynow" />
    </van-goods-action>
  </div>
</template>
<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      obj: null,
      count:0,
      total:0
    };
  },
  mounted() {
    this.$axios
      .get("/vue/getGoodDetail", {
        params: {
          goodId: this.$route.params.goodId
        }
      })
      .then(res => {
        this.obj = res.data.result;
        // console.log(res.data.result);
      });

      this.$axios.post("vue/getCartInfo",{
         username:sessionStorage.userInfo?JSON.parse(sessionStorage.userInfo).username:""
      }).then(res=>{
        console.log(res.data);
        this.total = res.data.total;
      })
  },
  methods: {
    callOne() {
      Toast("客服");
    },
    gotoCart() {
      Toast("去购物车");
    },
    addToshopcar() {
      this.$axios.post("vue/addCart",{
        username:sessionStorage.userInfo?JSON.parse(sessionStorage.userInfo).username:"",
        goodId:this.$route.params.goodId,
        goodInfo:this.obj,
        count:this.count
      }).then(res=>{
        console.log(res.data.result);
        this.total+=this.count;
      })
    },
    paynow() {
      Toast("立即购买");
    }
  }
};
</script>